import React, { useEffect, useState, useRef } from "react";

import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, ObjectKeys } from "../../../stores/Utils";

import "./dashboard.css"
import "./styles.css"
import { AddAccount_Cmpt, UpdAccount_Cmpt, AddSubAccount_Cmpt, UpdSubAccount_Cmpt, ViewSubMayor_Cmpt } from "..";
import { OpenModal } from "../../../components/DialogHRM";
import { getall_subaccount, getall_account, sortSubAccounts } from "../services/server_fetching";
import { SvgIcon_Cmpt } from "../../../components";

const UpdAccount = UpdAccount_Cmpt();

const AddSubAccount = AddSubAccount_Cmpt();

const UpdSubAccount = UpdSubAccount_Cmpt();

const ViewSubMayor = ViewSubMayor_Cmpt();
const Svg = SvgIcon_Cmpt();


const AccountItem  = ({ data, elmId, openUpdateView }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			// let frm2 = {"firstName":"Belisario","lastName":"Gonzalez Arevalo","genre":"Male","dateOfBirth":"06/02/1976","placeOfBirth":{"city":"Holguin","state":"Holguin"},"currentAddress":{"No":"4307","street":"Estate Dr","city":"Louisville","state":"Kentucky","zipcode":"40216"},"dob":202536000000}
			//updStore(frmNm, frm2);
			set_obs(gen16CodeId())
		}
	});

	
	const updObs =  () => {
		set_obs(gen16CodeId())
	}

	const refreshSubAcc =  () => {
		getall_subaccount(updObs)
	}

	const refreshAccs =  () => {
		getall_account(updObs)
	}

	const update = async () => {
		updStore("upd_account", data);
		updStore("upd_account_bck", JSON.parse(JSON.stringify(data)));
		let modaldata = {};
		modaldata["zIndex"] = 450;
		modaldata["height"] = "80vh";
		// data['observeResize']=true;
		modaldata["props"] = { minHeight: "1vh", overlay: true };
		modaldata["content"] = <UpdAccount confirm={refreshAccs} />
		
		OpenModal(modaldata);
	}

	
	const openAddSubAcc =  () => {
		updStore("account_rel_subacc", data);
		let modaldata = {};
		modaldata["zIndex"] = 450;
		modaldata["height"] = "80vh";
		// data['observeResize']=true;
		modaldata["props"] = { minHeight: "1vh", overlay: true };
		modaldata["content"] = 
			<AddSubAccount confirm={refreshSubAcc} />
		OpenModal(modaldata);
	}


	const openSubMayor =  (sba) => {
		updStore("submayor_list", null);
        updStore("submayor_group", null);
		let accountForm = {
			account: data.accountId
		}
		if(sba){
			accountForm["subAccount"] = sba;
		}
		updStore("account_sub_mayor", data);
		let modaldata = {};
		modaldata["zIndex"] = 450;
		modaldata["height"] = "80vh";
		// data['observeResize']=true;
		modaldata["props"] = { minHeight: "1vh", overlay: true };
		modaldata["content"] = 
			<ViewSubMayor confirm={refreshSubAcc} accountForm={accountForm}/>
		OpenModal(modaldata);
	}



	

	const updateSub = async (subacc) => {
		
		updStore("account_rel_subacc", data);
		updStore("upd_sub_account", subacc);
		updStore("upd_sub_account_bck", JSON.parse(JSON.stringify(subacc)));
		let modaldata = {};
		modaldata["zIndex"] = 450;
		modaldata["height"] = "80vh";
		// data['observeResize']=true;
		modaldata["props"] = { minHeight: "1vh", overlay: true };
		modaldata["content"] = <UpdSubAccount confirm={refreshSubAcc} />
		OpenModal(modaldata);
	}


	let subAcc = sortSubAccounts( getStore("sub_account_group")?.[data.accountId],"subAccount");
	
	// "Rs1CzHXPTG5TqbmH"
	//20231-6981


	return (
		<div className="item_account" 
		data-key={`${data.accountId}`}
		style={{
			"--hrm_palette_acc_type_color": data.type === "debit"? "var(--hrm_palette-success-fg)" :  data.type === "credit"? "var(--hrm_palette-blue-purple)":"#c62828"
		}}
		>
			<div className="_dsplFlx account_item_bx2 account_header">
				<div className="_dsplFlx" style={{minWidth:350, padding: "5px 0 0"}} onClick={update}>
					
					<div className="icon_acc_type">
						<Svg 
							name={data?.type === "debit"? "alpha-d-circle" :  data?.type === "credit"? "alpha-c-circle": "alert"  } 
							color={"var(--hrm_palette_acc_type_color)"}
							size={30}
						/>
					</div>
					<div className="code" >
						{data?.code} 
					</div>
					<div className="name">
						{data?. name}
					</div>

				</div>
				<div className="flexSpace"/>
				<div className=" addSbAcc purple" onClick={openAddSubAcc}>
					Agregar SubCuenta
				</div>
				<div className=" addSbAcc purple" onClick={()=>openSubMayor(null)}>
					Submayor
				</div>
			</div>
			{subAcc?.length && 
			<div >
				{ subAcc?.map(subacc=>{
					if(!subacc.subAccount){
						return null
					}
					return(
						<div 
							data-key={`${data.sub_accountId}`}
							key={subacc.sub_accountId} 
							className="_dsplFlx account_item_bx2"  
							style={{marginLeft:"65px"}}
						>
							<div className="_dsplFlx"  onClick={()=>updateSub(subacc)}>
								<div className="code" >
									{subacc.subAccount}
								</div>
								<div className="name">
									{subacc.subAccountName}
								</div>
							</div>
							<div className="flexSpace"/>
							<div className=" addSbAcc purple" onClick={()=>openSubMayor(subacc.sub_accountId)}>
								Submayor
							</div>
						</div>
					)
				})}
			</div>
			}
		</div>
	)
}
export default AccountItem;